


.App {
  margin: 1rem;
  /* text-align: center; */
  font-family: "Open Sans Condensed";
  font-stretch: extra-expanded;
  font-size: 1.4rem;
}

.calculatorInputs {
  width: 200px;
  padding: 10px;
  border-radius: 10px;
  border: none;
}

.calculatorButtons {
  width: 200px;
  padding: 5px;
  border-radius: 10px;
  background-color: grey;
  border: none;
  color: white;
  font-size: 1.5rem;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 30s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

